.addProject {
  padding: 4px 0;
  cursor: pointer;
  display: flex;
  align-items: center;

  &:not(:focus-visible) {
    outline: none;
  }
}

.wrapper {
  padding-left: 13px;
  padding-right: 20px;
  margin-top: 20px;
}

.plus {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--main-color);
}

.text {
  display: inline-block;
  color: #545454;
  font-size: 14px;
  line-height: 17px;
  margin-left: 15px;
}

.input {
  margin-bottom: 10px;
}

.name {
  width: 100%;
  border: 1px solid #ddd;
  background-color: white;
  border-radius: 3px;
  height: 35px;
  padding-left: 10px;
  padding-right: 10px;
}

.buttons {
  display: flex;
  justify-content: space-between;
}
